//日期工具
import dateTool from './assets/js/Tools.min.js';
Vue.prototype.$dateTool = dateTool;

//手指滑动
function moveS(fx, id) {
  var dom = document;
  if (id) {
    dom = document.getElementById(id);
  }
  var X, Y, SX, EX, SY, EY;
  // dom.removeEventListener('touchstart', startM);
  // dom.removeEventListener('touchmove', startE);
  return new Promise((resolve) => {
    dom.addEventListener('touchstart', startM);
    dom.addEventListener('touchmove', startE);

    function startM(e) {
      // e.preventDefault();
      e.stopPropagation()
      SX = e.touches[0].screenX
      SY = e.touches[0].screenY
    }

    /* function remove() {
      dom.removeEventListener('touchmove', startE);
      dom.removeEventListener('touchstart', startM);
    } */

    function startE(e) {
      // e.preventDefault();
      e.stopPropagation();
      EX = e.changedTouches[0].screenX;
      EY = e.changedTouches[0].screenY;
      X = parseInt(EX) - parseInt(SX)
      Y = parseInt(EY) - parseInt(SY)
      if (Math.abs(X) < parseInt(screen.height / 5)) {
        return;
      }
      switch (true) {
        case fx == 'right':
          if (Math.abs(X) > Math.abs(Y) && X > 0) {
            resolve();
          }
          break;
        case fx == 'left':
          if (Math.abs(X) > Math.abs(Y) && X < 0) {
            resolve();
          }
          break;
        case fx == 'up':
          if (Math.abs(Y) > Math.abs(X) && Y < 0) {
            resolve();
          }
          break;
        case fx == 'down':
          if (Math.abs(Y) > Math.abs(X) && Y > 0) {
            resolve();
          }
          break;
      }
    }

  });
}
window.$moveS = moveS;

function scrollEvent(that) {
  var afterS = event.target.scrollTop;
  var a = afterS - that.beforeS;
  if (a > 0) {
    if (
      event.target.scrollTop + event.target.offsetHeight >=
      event.target.scrollHeight
    ) {
      that.$toast({
        message: "已经到达底部",
        duration: 1000
      });
    }
  } else if (a < 0) {
    if (
      event.target.scrollTop + event.target.offsetHeight <=
      event.target.offsetHeight
    ) {
      that.$toast({
        message: "已经到达顶部",
        duration: 1000
      });
    }
  }
  that.beforeS = afterS;
}
Vue.prototype.$scrollE = scrollEvent;

function scrollEvent2(that) {
  var afterS = event.target.scrollTop;
  var a = afterS - that.beforeS;
  if (a > 0) {
    that.timeF = false;
    if (
      event.target.scrollTop + event.target.offsetHeight >=
      event.target.scrollHeight
    ) {
      that.timeF = false;
      that.$toast({
        message: "已经到达底部",
        duration: 1000
      });
    }
  } else if (a < 0) {
    that.timeF = true;
    if (
      event.target.scrollTop + event.target.offsetHeight <=
      event.target.offsetHeight
    ) {
      that.timeF = true;
      that.$toast({
        message: "已经到达顶部",
        duration: 1000
      });
    }
  }
  that.beforeS = afterS;
}
Vue.prototype.$scrollE2 = scrollEvent2;
/*---------------自定义-End---------------*/


