<template>
  <div class="wsh" id="wsh">
    <div class="content">
      <div class="content2" @scroll="$scrollE2(that)">
        <transition-group tag="ul" appear name="s">
          <liV
            v-for="(item,i) in data"
            :key="item.dh"
            :msg="item"
            type="wsh"
            :index="i"
            @liClick="liClick"
            @xgClick="xgClick"
            @delClick="delClick"
            @shClick="shClick"
          ></liV>
        </transition-group>
      </div>
      <transition name="s">
        <TimeV @confirm="confirmTime" v-show="timeF"></TimeV>
      </transition>
    </div>
  </div>
</template>
<script type="text/javascript">
export default {
  data() {
    return {
      msg: {
        ksrq: this.$moment(new Date()),
        jsrq: this.$moment(new Date()),
        zt: "未审核"
      },
      data: [],
      beforeS: "",
      that: this,
      timeF: true,
      move: false
    };
  },
  created() {
    this.submit();
  },
  mounted() {
    
  },
  watch: {
    data(newVal) {
      
    }
  },
  methods: {
    liClick(option) {
      this.$emit("mxShow", option);
    },
    xgClick(option) {
      option.type2 = "xg";
      this.$emit("mxShow", option);
    },
    delClick(option) {
      this.$dialog
        .confirm({
          title: "删除",
          message: "确认删除该单吗？"
        })
        .then(() => {
          this.$request("Jxc/sccpxsfh", { dh: option.dh })
            .then(res => {
              if (res.status == 1) {
                this.data.splice(option.index, 1);
                this.$toast({
                  message: "删除成功！",
                  duration: 1000
                });
              } else {
                this.$toast({
                  message: "删除失败！",
                  duration: 1000
                });
              }
            })
            .catch(() => {
              this.$toast({
                message: "请求服务器失败！",
                duration: 1000
              });
            });
        });
    },
    shClick(option) {
      this.$dialog
        .confirm({
          title: "审核",
          message: "确认审核该单吗？"
        })
        .then(() => {
          this.$request("Jxc/shcpxsfh", { dh: option.dh })
            .then(res => {
              if (res.status == 1) {
                this.data.splice(option.index, 1);
                this.$toast({
                  message: "审核成功！",
                  duration: 1000
                });
              } else {
                this.$toast({
                  message: "审核失败！",
                  duration: 1000
                });
              }
            })
            .catch(() => {
              this.$toast({
                message: "请求服务器失败！",
                duration: 1000
              });
            });
        });
    },
    submit() {
      this.$request("Jxc/cxcpxsfhwsh", this.msg)
        .then(res => {
          if (res.length == 0 || !res) {
            this.$toast({
              message: "暂无记录",
              duration: 1000
            });
          } else if (res.length > 0 && res instanceof Array) {
            this.data = res;
          }
        })
        .catch(() => {
          this.$toast({
            message: "请求服务器失败！",
            duration: 1000
          });
        });
    },
    confirmTime(rq) {
      this.msg.ksrq = rq.startDay;
      this.msg.jsrq = rq.endDay;
      this.submit();
    }
  },
  components: {}
};
</script>

<style lang="scss" scoped>
.s-enter,
.s-leave-to {
  opacity: 0;
  transform: translateY(100%);
}

.s-enter-active,
.s-leave-active {
  transition: all 0.5s ease;
}
.s-leave-active {
  position: absolute;
}
.s-move {
  transition: all 0.5s ease;
}
.wsh {
  flex: 1;
  width: 100%;
  overflow: hidden;
  .content {
    width: 100%;
    height: 100%;
    position: relative;
    display: flex;
    padding-bottom: 5px;
    flex-direction: column;
    box-sizing: border-box;
  }
  .content2 {
    flex: 1;
    width: 100%;
    box-sizing: border-box;
    overflow: hidden;
    overflow-y: auto;
  }
  ul {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
</style>
