<template>
  <div class="ysh" id="ysh">
    <div class="content">
      <div class="content2" @scroll="$scrollE2(that)">
        <transition-group tag="ul" appear name="s">
          <liV
            v-for="(item,i) in data"
            :key="item.dh"
            :index="i"
            :msg="item"
            type="ysh"
            @liClick="liClick"
            :move="false"
          ></liV>
        </transition-group>
      </div>
      <transition>
        <TimeV @confirm="confirmTime" v-show="timeF"></TimeV>
      </transition>
    </div>
  </div>
</template>
<script type="text/javascript">
export default {
  data() {
    return {
      msg: {
        ksrq: this.$moment(new Date()),
        jsrq: this.$moment(new Date()),
        zt: "已审核"
      },
      data: [],
      that: this,
      timeF: true
    };
  },
  created() {
    this.submit();
  },
  methods: {
    liClick(option) {
      this.$emit("mxShow", option);
    },
    submit() {
      this.$request("Jxc/cxcpxsfhysh", this.msg)
        .then(res => {
          if (res.length == 0 || !res) {
            this.$toast({
              message: "暂无记录",
              duration: 1000
            });
          } else if (res.length > 0 && res instanceof Array) {
            this.data = res;
          }
        })
        .catch(() => {
          this.$toast({
            message: "请求服务器失败！",
            duration: 1000
          });
        });
    },
    confirmTime(rq) {
      this.msg.ksrq = rq.startDay;
      this.msg.jsrq = rq.endDay;
      this.submit();
    }
  },
  components: {}
};
</script>

<style lang="scss" scoped>
.s-enter,
.s-leave-to {
  opacity: 0;
  transform: translateY(100%);
}
.s-enter-active,
.s-leave-active {
  transition: all 0.5s ease;
}
.s-leave-active {
  position: absolute;
}
.s-move {
  transition: all 0.5s ease;
}
.ysh {
  flex: 1;
  width: 100%;
  overflow: hidden;
  .content {
    width: 100%;
    height: 100%;
    position: relative;
    display: flex;
    padding-bottom: 5px;
    flex-direction: column;
    box-sizing: border-box;
    overflow: hidden;
  }
  .content2 {
    flex: 1;
    width: 100%;
    box-sizing: border-box;
    overflow: hidden;
    overflow-y: auto;
  }
  ul {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
</style>
