<template>
  <div class="mx" id="mx">
    <div class="mxContent">
      <van-nav-bar title="明细">
        <img
          src="../../assets/img/back.png"
          alt
          slot="left"
          style="height:20px"
          @click="$emit('show')"
        />
      </van-nav-bar>
      <div class="middleContent">
        <div class="Msg">
          <div class="MsgBox">
            <div
              class="Name"
              @click="(((msg.type == 'wsh' && msg.type2 == 'xg') || (msg.type == 'tj')) && yydhF) ? selectName() : ''"
            >
              客户：
              <span>{{khmc}}</span>
              <van-icon name="arrow-down" size="15px" style="position:absolute;right:30px" />
            </div>
            <div
              class="ckmc"
              @click="(((msg.type == 'wsh' && msg.type2 == 'xg') || (msg.type == 'tj')) && yydhF) ? selectCkmc() : ''"
            >
              仓库：
              <span>{{ckmc}}</span>
              <van-icon name="arrow-down" size="15px" style="position:absolute;right:30px" />
            </div>
          </div>
          <div class="MsgBox2">
            <div>
              有效条码：
              <span style="color:#4382df">{{yxtm}}</span>
            </div>
            <div>
              无效条码：
              <span style="color:#ee0a24">{{wxtm}}</span>
            </div>
          </div>
        </div>
        <div class="tabC">
          <van-tabs v-model="active" title-active-color="#0894ec" title-inactive-color="#999">
            <van-tab name="hz" title="汇总" style="flex:1"></van-tab>
            <van-tab name="mx" title="明细"></van-tab>
          </van-tabs>
          <transition-group tag="div" class="ulBox" name="h">
            <div class="mxlist" :key="'hz'" v-show="listSwitch" @scroll="$scrollE(that)">
              <transition-group tag="ul" appear name="s">
                <li :key="item.ysmc + item.gg" v-for="(item) in data2">
                  <div>品名：{{item.pm}}</div>
                  <div>颜色：{{item.ysmc}}</div>
                  <div>单位：{{item.dw}}</div>
                  <div>规格：{{item.gg}}</div>
                  <div>匹数：{{item.ps}}</div>
                  <div>重量：{{item.sl}}</div>
                </li>
              </transition-group>
            </div>
            <div class="mxlist" :key="'mx'" v-show="!listSwitch" @scroll="$scrollE(that)">
              <transition-group tag="ul" appear name="s">
                <li :key="item.id" v-for="(item,i) in data" v-longTouch="{that,i}">
                  <div style="position:relative">
                    品名：{{item.pm}}
                    <div style="position:absolute;top:0;left:0;width:100%;height:100%;"></div>
                  </div>
                  <div style="position:relative">
                    颜色：{{item.ysmc}}
                    <div style="position:absolute;top:0;left:0;width:100%;height:100%;"></div>
                  </div>
                  <div style="position:relative">
                    单位：{{item.dw}}
                    <div style="position:absolute;top:0;left:0;width:100%;height:100%;"></div>
                  </div>
                  <div style="position:relative">
                    规格：{{item.gg}}
                    <div style="position:absolute;top:0;left:0;width:100%;height:100%;"></div>
                  </div>
                  <div style="position:relative">
                    匹数：{{item.ps}}
                    <div style="position:absolute;top:0;left:0;width:100%;height:100%;"></div>
                  </div>
                  <div style="position:relative">
                    重量：{{parseInt(item.sl)}}
                    <div style="position:absolute;top:0;left:0;width:100%;height:100%;"></div>
                  </div>
                  <div
                    style="width:100%"
                    @click.stop="(msg.type == 'tj' || (msg.type =='wsh' && msg.type2 == 'xg')) ? selectYydh(i) : ''"
                  >
                    引用单号：{{item.yydh}}
                    <van-icon
                      name="arrow-down"
                      size="12px"
                      style="margin-left:10px"
                      v-if="(msg.type == 'tj' || (msg.type =='wsh' && msg.type2 == 'xg'))"
                    />
                  </div>
                </li>
              </transition-group>
            </div>
          </transition-group>
          <transition-group tag="div" class="gnBtn" v-if="btnShow">
            <div style="background-color:#1989fa;width:25%" v-if="wshF" @click="sys" key="sys">扫一扫</div>
            <div
              style="background-color:#1989fa;width:25%"
              v-if="wshF"
              key="tjtm"
              @click="tjtm"
            >添加条码</div>
            <div style="background-color:#07c160;width:25%" v-if="bcF" key="bc" @click="bc">保存</div>
            <div style="background-color:#ee0a24;width:50%" key="zf" v-if="yshF" @click="zf">作废</div>
            <!-- <div style="background-color:#1989fa;width:40%" key="zf" v-if="yshF">结单</div> -->
          </transition-group>
        </div>
      </div>
      <van-dialog
        v-model="show"
        title="请输入条码"
        :showConfirmButton="confirmTmShow"
        showCancelButton
        @confirm="confirmTm"
      >
        <input
          type="text"
          style="border-radius:4px;margin:10px 0 20px 0;height:26px;"
          v-model="tm"
          placeholder="请输入条码"
        />
      </van-dialog>
      <transition name="s">
        <SelectV
          @liClick="selectClick"
          :attribute="atr"
          :url="url"
          :title="selectTitle"
          v-if="selShow"
          @show="selShow = !selShow"
          right-color="#fff"
          :yyMsg="yyMsg"
        ></SelectV>
      </transition>
    </div>
  </div>
</template>

<script type="text/javascript">
export default {
  props: ["msg"],
  directives: {
    longTouch: {
      inserted(dom, bind) {
        var that = bind.value.that;
        var i = 0,
          timerID = null;
        dom.addEventListener("touchstart", function() {
          timerID = setInterval(function() {
            i += 1;
            if (i >= 3) {
              that.liClick(bind.value.i);
              clearInterval(timerID);
              return;
            }
          }, 500);
        });
        dom.addEventListener("touchend", function() {
          clearInterval(timerID);
        });
      }
    }
  },
  data() {
    return {
      that: this,
      khmc: "",
      ckmc: "",
      yxtm: 0,
      wxtm: 0,
      active: "hz",
      bcF: false,
      wshF: false,
      yshF: false,
      selShow: false,
      show: false,
      confirmTmShow: false,
      btnShow: true,
      tm: "",
      data: [],
      that: this,
      listSwitch: true,
      atr: "",
      url: "",
      selectTitle: "",
      yydhF: true,
      yyMsg: {},
      index: "",
      a: []
    };
  },
  computed: {
    data2() {
      var b = {};
      this.data.forEach(item => {
        if (!b[item.pm + item.ysmc + item.gg]) {
          b[item.pm + item.ysmc + item.gg] = [];
          b[item.pm + item.ysmc + item.gg].push(item);
        } else if (b[item.pm + item.ysmc + item.gg]) {
          b[item.pm + item.ysmc + item.gg].push(item);
        }
      });
      var a = [];
      var c = {};
      for (let key in b) {
        c.gg = b[key][0].gg;
        c.pm = b[key][0].pm;
        c.ysmc = b[key][0].ysmc;
        c.dw = b[key][0].dw;
        c.ps = 0;
        c.sl = 0;
        b[key].forEach(item => {
          c.sl += parseFloat(item.sl);
          c.ps += parseFloat(item.ps);
        });
        var d = JSON.parse(JSON.stringify(c));
        a.push(d);
      }
      return a;
    }
  },
  created() {
    window.scan = this.scan;
    this.submit();
    if (this.msg.type == "tj" || this.msg.type2 == "xg") {
      this.wshF = true;
    } else if (this.msg.type == "ysh") {
      this.yshF = true;
    } else {
      this.btnShow = false;
    }
    if (this.msg.dh) {
      this.khmc = this.msg.msg.khmc;
      this.ckmc = this.msg.msg.ckmc;
    }
  },
  methods: {
    selectYydh(i) {
      this.url = "Jxc/cxyydh";
      this.atr = "yydh";
      this.selectTitle = "引用单号";
      this.selShow = !this.selShow;
      this.index = i;
      if (this.msg.msg) {
        this.yyMsg = JSON.parse(JSON.stringify(this.msg.msg));
      }
      Object.assign(this.yyMsg, {
        ysmc: this.data[i].ysmc,
        pm: this.data[i].pm,
        gg: this.data[i].gg,
        khmc: this.khmc,
        ckmc: this.khmc
      });
      this.bcF = true;
    },
    selectName() {
      this.url = "Index/cxkh";
      this.atr = "khmc";
      this.selectTitle = "客户";
      this.selShow = !this.selShow;
    },
    selectCkmc() {
      this.url = "Index/cxck";
      this.atr = "ckmc";
      this.selectTitle = "仓库";
      this.selShow = !this.selShow;
    },
    scan(tm) {
      this.tm = tm;
      this.cxtm();
      this.$toast({
        message: "扫码成功！",
        duration: 1000
      });
    },
    confirmTm() {
      this.cxtm();
    },
    tjtm() {
      this.show = true;
      this.active = "mx";
    },
    zf() {
      this.$request("Jxc/zfxsfhd", {
        dh: this.msg.msg.dh
      })
        .then(res => {
          if (res.status == 1) {
            this.$toast({
              message: "作废成功！",
              duration: 1000
            });
            this.bcF = false;
            this.$emit("show");
          } else {
            this.$toast({
              message: "作废失败！",
              duration: 1000
            });
          }
        })
        .catch(() => {
          this.$toast({
            message: "请求服务器失败！",
            duration: 1000
          });
        });
    },
    bc() {
      if (this.msg.type == "wsh") {
        this.$request("Jxc/cpxsfhxg", {
          msg: this.data,
          dh: this.msg.msg.dh,
          ckmc: this.ckmc,
          khmc: this.khmc
        })
          .then(res => {
            if (res.status == 1) {
              this.$toast({
                message: "保存成功！",
                duration: 1000
              });
              this.bcF = false;
            } else {
              this.$toast({
                message: "保存失败！",
                duration: 1000
              });
            }
          })
          .catch(() => {
            this.$toast({
              message: "请求服务器失败！",
              duration: 1000
            });
          });
      } else if (this.msg.type == "tj") {
        if (this.khmc == "") {
          this.$toast({
            message: "客户名称不能为空！",
            duration: 1000
          });
          return;
        }
        if (this.ckmc == "") {
          this.$toast({
            message: "仓库名称不能为空！",
            duration: 1000
          });
          return;
        }
        if (this.data.length == 0) {
          this.$toast({
            message: "明细不能为空！请扫入或添加条码！",
            duration: 1000
          });
          return;
        }
        this.$request("Jxc/cpxsfh", {
          msg: this.data,
          ckmc: this.ckmc,
          khmc: this.khmc
        })
          .then(res => {
            if (res.status == 1) {
              this.$toast({
                message: "保存成功！",
                duration: 1000
              });
              this.bcF = false;
            } else {
              this.$toast({
                message: "保存失败！",
                duration: 1000
              });
            }
          })
          .catch(() => {
            this.$toast({
              message: "请求服务器失败！",
              duration: 1000
            });
          });
      }
    },
    cxtm() {
      this.$request("Jxc/cxtm", { tmbh: this.tm }).then(res => {
        if (res.length == 0) {
          this.$toast({
            message: "无效条码！",
            duration: 1000
          });
          this.wxtm += 1;
          this.tm = "";
          return;
        }
        if (res && res[0].khmc == "") {
          var f = true;
          this.data.forEach(item => {
            if (item.tmbh == res[0].tmbh) {
              f = false;
            }
          });
          if (f) {
            this.data.push(res[0]);
            this.yxtm += 1;
            this.bcF = true;
            this.tm = "";
          } else {
            this.$toast({
              message: "该条码已存在，请勿重复添加！",
              duration: 1000
            });
            this.tm = "";
          }
        } else if (res[0].khmc != "") {
          this.$toast({
            message: "条码已发货，无效！",
            duration: 1000
          });
          this.wxtm += 1;
        }
      });
    },
    liClick(i) {
      this.$dialog
        .confirm({
          title: "删除",
          message: "确认删除该条信息吗？"
        })
        .then(() => {
          this.data.splice(i, 1);
          this.bcF = true;
        })
        .catch(() => {});
    },
    submit() {
      if (this.msg.dh) {
        this.$request("Jxc/cxcpxsfhmx", { dh: this.msg.dh })
          .then(res => {
            if (res.length != 0 && res) {
              this.data = res;
              this.data.forEach(item => {
                if (item.yydh != "") {
                  this.yydhF = false;
                }
              });
              /* for (let i = 0, len = this.data.length; i < len; i++) {
                this.a.push(i);
              } */
            } else {
              this.$toast({
                message: "暂无记录！",
                duration: 1000
              });
            }
          })
          .catch(() => {
            this.$toast({
              message: "请求服务器失败！",
              duration: 1000
            });
          });
      }
    },
    sys() {
      this.bcF = true;
      this.active = "mx";
      webview = plus.webview
        .create("scan.html", "scan", {
          backButtonAutoControl: "close",
          titleNView: {
            type: "float",
            backgroundColor: "rgba(215,75,40,0)",
            titleText: "扫一扫",
            titleColor: "#FFFFFF",
            autoBackButton: true,
            buttons: [
              {
                text: "相册",
                fontSize: "16px",
                onclick: "javascript:pick()"
              }
            ]
          }
        })
        .show("slide-in-right");
    },
    selectClick(item) {
      if (this.selectTitle == "客户") {
        this.khmc = item;
      } else if (this.selectTitle == "仓库") {
        this.ckmc = item;
      } else if (this.selectTitle == "引用单号") {
        if (item && item != "") {
          this.data[this.index].yydh = item;
          if (!this.a.includes(this.index)) {
            this.a.push(this.index);
            for (var i = this.index + 1, len = this.data.length; i < len; i++) {
              if (
                this.data[this.index].pm == this.data[i].pm &&
                this.data[this.index].gg == this.data[i].gg &&
                this.data[this.index].ysmc == this.data[i].ysmc
              ) {
                this.data[i].yydh = this.data[this.index].yydh;
              }
            }
          }
        }
      }
    }
  },
  watch: {
    tm(newVal) {
      if (newVal != "") {
        this.confirmTmShow = true;
      } else {
        this.confirmTmShow = false;
      }
    },
    active(newVal) {
      if (newVal == "hz") {
        this.listSwitch = true;
      } else if (newVal == "mx") {
        this.listSwitch = false;
      }
    }
  },
  components: {},
  mounted() {
    $moveS("right", "mx").then(() => {
      this.$emit("show");
    });
  }
};
</script>

<style lang="scss" scoped>
.h-enter,
.h-leave-to {
  opacity: 0;
  transform: translateX(-100%);
}

.h-enter-active,
.h-leave-active {
  transition: all 0.5s ease;
}
.h-leave-active {
  position: absolute;
}
.h-move {
  transition: all 0.5s ease;
}
.s-enter,
.s-leave-to {
  opacity: 0;
  transform: translateY(100%);
}

.s-enter-active,
.s-leave-active {
  transition: all 0.5s ease;
}
.s-leave-active {
  position: absolute;
}
.s-move {
  transition: all 0.5s ease;
}
.van-nav-bar {
  background-color: white !important;
}
.mx {
  font-size: 14px;
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  z-index: 2;
  background-color: #f9f9f9;
  .mxContent {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    .middleContent {
      flex: 1;
      width: 100%;
      display: flex;
      align-items: center;
      flex-direction: column;
      .Msg {
        width: 96%;
        height: 80px;
        box-shadow: 0 0 8px #e6e6e6;
        span {
          color: #666;
        }
        .MsgBox {
          color: #999;
          margin-top: 6px;
          width: 100%;
          border-radius: 8px 8px 0 0;
          height: 40px;
          display: flex;
          background-color: white;
          div {
            flex: 1;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: flex-start;
            padding-left: 10px;
            box-sizing: border-box;
            position: relative;
          }
        }
        .MsgBox2 {
          width: 100%;
          border-radius: 0 0 8px 8px;
          height: 40px;
          display: flex;
          background-color: white;
          color: #999;
          div {
            flex: 1;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: flex-start;
            padding-left: 10px;
            box-sizing: border-box;
          }
        }
      }
      .tabC {
        margin-top: 8px;
        flex-shrink: 0;
        flex-grow: 1;
        height: 200px;
        width: 100%;
        display: flex;
        flex-direction: column;
        overflow: hidden;
        .van-tabs {
          flex-shrink: 0;
          width: 96%;
          margin-left: 2%;
          border-radius: 8px 8px 0 0;
          height: 44px;
          overflow: hidden;
        }
        .ulBox {
          flex: 1;
          width: 96%;
          margin-left: 2%;
          margin-bottom: 8px;
          background-color: white;
          border-radius: 0 0 8px 8px;
          overflow: hidden;
        }
        .mxlist {
          height: 100%;
          width: 100%;
          overflow: hidden;
          overflow-y: scroll;
        }
        ul {
          height: 100%;
          display: flex;
          flex-direction: column;
          width: 100%;
          li:first-child {
            margin-top: 8px;
          }
          li:last-child {
            margin-bottom: 8px;
          }
          li {
            flex-shrink: 0;
            height: 100px;
            width: 96%;
            margin-bottom: 5px;
            margin-left: 2%;
            background-color: white;
            border-radius: 8px;
            margin-top: 5px;
            display: flex;
            flex-wrap: wrap;
            align-content: space-around;
            box-shadow: 0 0 8px #e6e6e6;
            -webkit-touch-callout: none; /*系统默认菜单被禁用*/
            -webkit-user-select: none; /*webkit浏览器*/
            -khtml-user-select: none; /*早期浏览器*/
            -moz-user-select: none; /*火狐*/
            -ms-user-select: none; /*IE10*/
            user-select: none;
            div {
              width: 50%;
              height: 22px;
              box-sizing: border-box;
              font-size: 12px;
              display: flex;
              align-items: center;
              padding-left: 15px;
            }
          }
        }
      }
      .gnBtn {
        height: 46px;
        width: 96%;
        margin-left: 2%;
        margin-bottom: 5px;
        display: flex;
        align-items: center;
        justify-content: space-around;
        div {
          display: flex;
          align-items: center;
          justify-content: center;
          border-radius: 8px;
          height: 40px;
          color: white;
        }
      }
    }
  }
}
</style>
