<template>
  <div class="select" id="sl">
    <van-nav-bar :title="'选择'+title" @click-right="onClickRight" style="color:#fff">
      <van-icon name="arrow-down" slot="right" :color="rightColor || ''" />
    </van-nav-bar>
    <form action="/">
      <van-search v-model="gjz" placeholder="请输入搜索关键词" />
    </form>
    <ul @scroll="scroll">
      <li
        v-for="(item,i) in msg2"
        :key="item[attribute]+i"
        @click="liClick(item[attribute])"
        :style="''"
      >
        {{item[attribute]}}
        <span></span>
      </li>
    </ul>
  </div>
</template>
<script type="text/javascript">
export default {
  name: "SelectV",
  data() {
    return {
      beforeS: "",
      arr: [],
      msg: [],
      msg2: [],
      msg3: [],
      i: 0,
      gjz: "",
      len: "",
      attribute2: ""
    };
  },
  props: ["url", "title", "attribute", "rightColor", "yyMsg"],
  created() {
    this.submit(this.url);
  },
  mounted() {
    $moveS("right", "sl").then(res => {
      this.$emit("show");
    });
  },
  methods: {
    liClick(item) {
      this.$emit("show");
      this.$emit("liClick", item);
    },
    onClickRight() {
      this.$emit("show");
    },
    submit(url) {
      var b = {};
      if (this.yyMsg) {
        Object.assign(b, this.yyMsg);
      }
      this.$request(url, b)
        .then(res => {
          if (res.length == 0) {
            this.$toast({
              message: "暂无记录！",
              duration: 1500
            });
            return;
          }
          if (this.lx) {
            (this.msg2[0] = {}), (this.msg2[0][this.attribute] = "全部");
          }
          this.len = parseInt(res.length / 20);
          var step = 0;
          this.msg = res;
          if (res.length % 20 != 0) {
            this.len += 1;
          }
          for (let i = 0; i < this.len; i++) {
            this.arr.push(res.slice(step, step + 20));
            step += 20;
          }
          this.arr[this.i].forEach(item => {
            this.msg2.push(item);
          });
          this.msg3 = this.msg2.slice(0, this.msg2.length);
        })
        .catch(err => {
          this.$indicator.close();
          this.$toast({
            message: "请求失败",
            duration: 1500
          });
        });
    },
    scroll() {
      var afterS = event.target.scrollTop,
        a = afterS - this.beforeS;
      if (a > 0) {
        if (
          event.target.scrollTop + event.target.offsetHeight >=
          event.target.scrollHeight
        ) {
          this.i += 1;
          if (this.i >= this.len) {
            this.$toast({
              message: "已经到达底部",
              duration: 1000
            });
            return;
          }
          if (this.i < this.len) {
            this.arr[this.i].forEach(item => {
              this.msg2.push(item);
            });
          }
          this.msg3 = this.msg2.slice(0, this.msg2.length);
        }
      } else if (a < 0) {
        if (
          event.target.scrollTop + event.target.offsetHeight <=
          event.target.offsetHeight
        ) {
          this.$toast({
            message: "已经到达顶部",
            duration: 1000
          });
        }
      }
      this.beforeS = afterS;
    }
  },
  watch: {
    gjz: function(newVal, oldVal) {
      if (newVal == "") {
        this.msg2 = this.msg3.slice(0, this.msg3.length);
        return;
      }
      this.msg2 = this.msg.filter(item => {
        if (item[this.attribute].includes(newVal)) {
          return item;
        }
      });
    }
  },
  components: {}
};
</script>

<style lang="scss" scoped>
.select {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  z-index: 999;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  ul {
    flex: 1;
    width: 100%;
    overflow: hidden;
    overflow-y: auto;
    li {
      height: 50px;
      font-size: 14px;
      color: #666;
      position: relative;
      text-align: center;
      line-height: 50px;
      span {
        display: block;
        height: 1px;
        width: 92%;
        background-color: #f8f8f8;
        position: absolute;
        bottom: 0;
        right: 4%;
      }
    }
  }
}
</style>
