<template>
  <li
    class="li"
    @click.stop="$emit('liClick',{dh:msg.dh,type:type,index:index,msg:msg})"
    v-move="that"
  >
    <div class="content">
      <div class="top">
        <van-icon
          name="manager-o"
          size="20px"
          style="margin-left:10px;margin-right:10px;"
          color="#007df2"
        />
        <p>{{msg.khmc}}</p>
        <div class="tag">{{msg.ch == 1 ? '已作废' : msg.ckmc}}</div>
        <span></span>
      </div>
      <div class="middle">
        <div>
          <p>
            <van-icon name="clock-o" size="14px" color="#007df2" style="margin-right:2px;" />日期
          </p>
          <p>
            <span class="span">{{msg.rq ? msg.rq.substring(0,11) : ''}}</span>
          </p>
          <span></span>
        </div>
        <div style="flex:2">
          <p>
            <van-icon name="label-o" size="14px" color="#007df2" style="margin-right:2px;" />单号
          </p>
          <p>
            <span class="span">{{msg.dh}}</span>
          </p>
          <span></span>
        </div>
        <div>
          <p>
            <van-icon name="cluster-o" size="14px" color="#007df2" style="margin-right:2px;" />
            {{msg.ch == 1 ? '仓库' : '流水'}}
          </p>
          <p>
            <span class="span">{{msg.ch == 1 ? msg.ckmc :msg.lsdh}}</span>
          </p>
          <span></span>
        </div>
      </div>
      <transition>
        <div class="button" v-show="flag">
          <div @click.stop="$emit('xgClick',{dh:msg.dh,type:type,index:index,msg:msg})">修改</div>
          <div @click.stop="$emit('delClick',{dh:msg.dh,type:type,index:index,msg:msg})">删除</div>
          <div @click.stop="$emit('shClick',{dh:msg.dh,type:type,index:index,msg:msg})">审核</div>
        </div>
      </transition>
    </div>
  </li>
</template>

<script type="text/javascript">
export default {
  name: "liV",
  props: ["msg", "index", "move", "type"],
  data() {
    return {
      flag: false,
      that: this
    };
  },
  directives: {
    move: {
      inserted(dom, bind) {
        if (bind.value.type == "wsh") {
          var X, Y, SX, EX, SY, EY;
          dom.addEventListener("touchstart", startM);
          dom.addEventListener("touchmove", startE);
          function startM(e) {
            e.stopPropagation();
            SX = e.touches[0].screenX;
            SY = e.touches[0].screenY;
          }
          function startE(e) {
            e.stopPropagation();
            EX = e.changedTouches[0].screenX;
            EY = e.changedTouches[0].screenY;
            X = parseInt(EX) - parseInt(SX);
            Y = parseInt(EY) - parseInt(SY);
            if (Math.abs(X) < parseInt(screen.height / 7)) {
              return;
            }
            if (Math.abs(X) > Math.abs(Y) && X > 0) {
              bind.value.flag = false;
            }
            if (Math.abs(X) > Math.abs(Y) && X < 0) {
              bind.value.flag = true;
            }
          }
        }
      }
    }
  },
  methods: {
    moveS(id, that) {
      var dom = document;
      if (id) {
        dom = document.getElementById(id);
      }
      var X, Y, SX, EX, SY, EY;
      dom.addEventListener("touchstart", startM);
      dom.addEventListener("touchmove", startE);
      function startM(e) {
        e.stopPropagation();
        SX = e.touches[0].screenX;
        SY = e.touches[0].screenY;
      }
      function startE(e) {
        e.stopPropagation();
        EX = e.changedTouches[0].screenX;
        EY = e.changedTouches[0].screenY;
        X = parseInt(EX) - parseInt(SX);
        Y = parseInt(EY) - parseInt(SY);
        if (Math.abs(X) < parseInt(screen.height / 7)) {
          return;
        }
        if (Math.abs(X) > Math.abs(Y) && X > 0) {
          that.move = false;
        }
        if (Math.abs(X) > Math.abs(Y) && X < 0) {
          that.move = true;
        }
      }
    }
  },
  components: {}
};
</script>

<style lang="scss" scoped>
.v-enter,
.v-leave-to {
  opacity: 0;
  transform: translateX(100%);
}

.v-enter-active,
.v-leave-active {
  transition: all 0.5s ease;
}
.v-leave-active {
  position: absolute;
}
.v-move {
  transition: all 0.5s ease;
}
.li {
  height: 110px;
  width: 96%;
  margin-top: 8px;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0 0 8px #e8e8e8;
  overflow: hidden;
  .content {
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
    .button {
      position: absolute;
      height: 100%;
      width: 120px;
      right: 0;
      top: 0;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      div {
        background-color: #0894ec;
        width: 100%;
        height: 30px;
        border-radius: 10px 0px 0px 10px;
        color: white;
        display: flex;
        font-size: 14px;
        justify-content: center;
        align-items: center;
      }
    }
    .top {
      height: 32px;
      width: 100%;
      position: relative;
      display: flex;
      align-items: center;
      .tag {
        display: flex;
        height: 14px;
        align-items: center;
        border-radius: 4px;
        padding: 3px 4px;
        font-size: 12px;
        border: solid 1px #007df2;
        position: absolute;
        right: 10px;
      }
      span {
        position: absolute;
        border-bottom: 1px solid #f0f0f0;
        width: 96%;
        bottom: 0;
        margin-left: 2%;
      }
    }
    .middle {
      height: 78px;
      width: 100%;
      display: flex;
      div:last-child {
        span {
          border: none;
        }
      }
      div {
        flex: 1;
        height: 100%;
        position: relative;
        display: flex;
        flex-direction: column;
        overflow: hidden;
        box-sizing: border-box;
        padding: 0 5px;
        p:first-child {
          color: #999;
          font-size: 12px;
          flex: 1;
          display: flex;
          justify-content: center;
          align-items: flex-end;
          flex-direction: row;
        }
        p {
          display: flex;
          height: 42px;
          width: 100%;
          font-size: 12px;
          box-sizing: border-box;
          padding: 0 5px;
          align-items: center;
          justify-content: center;
          flex-direction: column;
          .span {
            display: flex !important;
            flex-direction: column;
            flex-basis: 20px;
            width: 100%;
            overflow-x: auto;
            font-style: normal !important;
            position: static !important;
            border: none !important;
            transform: none !important;
            white-space: nowrap;
          }
        }
        span {
          position: absolute;
          right: 0;
          top: 50%;
          width: 1px;
          height: 50px;
          transform: translateY(-50%);
          border-right: 1px dashed #e5e5e5;
        }
      }
    }
  }
}
</style>
