<template>
  <div class="timep">
    <div class="div2" :style="flag.todayF ? 'color:#fff;background-color:#007df2' : ''" @click="change(0)">今天</div>
    <div class="div2" :style="flag.yesterdayF ? 'color:#fff;background-color:#007df2' : ''" @click="change(1)">昨天</div>
    <div class="div2" :style="flag.weekF ? 'color:#fff;background-color:#007df2' : ''" @click="change(2)">本周</div>
    <div class="div2" :style="flag.gdF ? 'color:#fff;background-color:#007df2' : ''" @click="change(3)">更多</div>
    <van-popup
      v-model="show"
      get-container="#wf"
      position="bottom"
      :style="{ height: '200px'}"
      round
    >
      <div class="div">
        <div>
          <span>
            <van-icon name="calender-o" size="18px" style="height:18px;" />
          </span>
          <input type="date" v-model="rq.startDay" />
          <span>至</span>
          <input type="date" v-model="rq.endDay" />
          <span></span>
        </div>
        <van-button type="info" @click="confirmTime">确认</van-button>
      </div>
    </van-popup>
  </div>
</template>
<script>
export default {
  name:'TimeV',
  data() {
    return {
      flag: {
        todayF: true,
        yesterdayF: false,
        weekF: false,
        gdF: false
      },
      rq: {
        startDay: this.$moment(new Date()),
        endDay: this.$moment(new Date())
      },
      show: false
    };
  },
  mounted() {},
  methods: {
    confirmTime() {
      this.show = !this.show;
      this.$emit("confirm", this.rq);
    },
    change(i) {
      for (var key in this.flag) {
        this.flag[key] = false;
      }
      switch (true) {
        case i == 0:
          this.flag.todayF = true;
          this.rq.startDay = this.$dateTool.today;
          this.rq.endDay = this.$dateTool.today;
          this.$emit("confirm", this.rq);
          break;
        case i == 1:
          this.flag.yesterdayF = true;
          this.rq.startDay = this.$dateTool.yesterday;
          this.rq.endDay = this.$dateTool.yesterday;
          this.$emit("confirm", this.rq);
          break;
        case i == 2:
          this.flag.weekF = true;
          this.rq.startDay = this.$dateTool.week.startDay;
          this.rq.endDay = this.$dateTool.week.endDay;
          this.$emit("confirm", this.rq);
          break;
        case i == 3:
          this.flag.gdF = true;
          this.show = !this.show;
          break;
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.div {
  position: absolute;
  width: 100%;
  height: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 10px 10px 0 0;
  button {
    width: 300px;
    height: 40px;
    /* display: block;
    appearance: none;
    border: none;
    background: none;
    background-color: #007df2;
    color: #ffffff;
    border-radius: 4px;
    font-size: 14px; */
  }
  div {
    margin-top: 30px;
    margin-bottom: 30px;
    width: 300px;
    height: 35.2px;
    border-radius: 4px;
    display: flex;
    font-size: 12px;
    border: 1px solid #dcdfe6;
    align-items: center;
    span:first-child {
      height: 35.2px;
      width: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    span {
      height: 35.2px;
      width: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  input {
    height: 30px;
    width: 105px;
    font-size: 12px;
    border: none;
    appearance: none;
    text-align: center;
  }
}
.timep {
  position: absolute;
  bottom: 10px;
  display: flex;
  width: 96%;
  height: 32px;
  margin-left: 2%;
  border-radius: 6px;
  background-color: #ffffff;
  border: solid #007df2 1px;
  overflow: hidden;
  .div2 {
    flex: 1;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    color: #666;
  }
}
</style>