import TimeV from './timeVue'
import SelectV from './selectVue'
import {
    Search
} from 'vant';
import {
    NavBar
} from 'vant';
import {
    Indicator
} from 'mint-ui';
import {
    Toast
} from 'vant';
/* export {
    TimeV,
}; */

//全量引入

const components = [
    TimeV,
    SelectV
]

function install(Vue) {
    Vue.use(Search);
    Vue.use(NavBar);
    Vue.use(Toast);
    Vue.prototype.$indicator = Indicator;
    components.forEach(component => {
        Vue.component(component.name, component)
    })
}

if (typeof window !== 'undefined' && window.Vue) {
    install(window.Vue);
}

export default {
    install,
    TimeV,
    SelectV,
}